"use client";

import { useState, useEffect } from "react";
import Image from "next/image";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Minus, Plus, Trash2, ChevronRight, PlusCircle } from "lucide-react";
import TopPicks from "@/components/home/TopPicks";
import { useCart, CartItem } from '@/context/CartContext';
import { formatPrice } from '@/lib/formatPrice';
import { AddressFormDialog } from "@/components/address/AddressFormDialog";
import { AddressCard } from "@/components/address/AddressCard";
import { Address } from "@/components/address/AddressFormDialog";

export default function Cart() {
  const [isClient, setIsClient] = useState(false);
  const [couponCode, setCouponCode] = useState("");
  const [couponApplied, setCouponApplied] = useState(false);
  
  const { 
    cart = [], 
    removeFromCart, 
    updateQuantity: contextUpdateQuantity, 
    getCartTotal,
    userLocation,
    deliveryFee,
    setUserLocation,
    calculateDeliveryFee
  } = useCart();
  
  // Address management state
  const [selectedAddressId, setSelectedAddressId] = useState<string | null>(null);
  const [isAddressDialogOpen, setIsAddressDialogOpen] = useState(false);
  const [editingAddress, setEditingAddress] = useState<Address | undefined>();
  const [savedAddresses, setSavedAddresses] = useState<Address[]>([]);
  const [isLocating, setIsLocating] = useState(false);
  const [locationError, setLocationError] = useState('');
  
  // Load saved addresses from localStorage on component mount
  useEffect(() => {
    const saved = localStorage.getItem('savedAddresses');
    if (saved) {
      const parsed = JSON.parse(saved);
      setSavedAddresses(parsed);
      
      // Set the first address as selected by default if none is selected
      if (parsed.length > 0 && !selectedAddressId) {
        const defaultAddress = parsed.find((addr: Address) => addr.isDefault) || parsed[0];
        setSelectedAddressId(defaultAddress.id);
        setUserLocation({
          address: `${defaultAddress.addressLine1}, ${defaultAddress.city}, ${defaultAddress.state} - ${defaultAddress.pincode}`,
          coordinates: defaultAddress.coordinates
        });
      }
    }
  }, []);
  
  // Auto-detect location on component mount if no saved addresses
  useEffect(() => {
    if (savedAddresses.length === 0 && navigator.geolocation) {
      setIsLocating(true);
      navigator.geolocation.getCurrentPosition(
        async (position) => {
          try {
            const response = await fetch(
              `https://nominatim.openstreetmap.org/reverse?format=json&lat=${position.coords.latitude}&lon=${position.coords.longitude}&addressdetails=1`
            );
            const data = await response.json();
            
            const newAddress: Address = {
              id: 'current-location',
              type: 'home',
              name: 'My Location',
              phone: '',
              addressLine1: data.display_name || 'Current Location',
              city: data.address?.city || data.address?.town || '',
              state: data.address?.state || '',
              pincode: data.address?.postcode || '',
              landmark: '',
              isDefault: true,
              coordinates: {
                lat: position.coords.latitude,
                lng: position.coords.longitude
              }
            };
            
            const updatedAddresses = [newAddress];
            setSavedAddresses(updatedAddresses);
            localStorage.setItem('savedAddresses', JSON.stringify(updatedAddresses));
            setSelectedAddressId(newAddress.id);
            setUserLocation({
              address: newAddress.addressLine1,
              coordinates: newAddress.coordinates
            });
            
          } catch (error) {
            console.error('Error getting address:', error);
            setLocationError('Could not determine your location. Please add an address manually.');
          } finally {
            setIsLocating(false);
          }
        },
        (error) => {
          console.error('Error getting location:', error);
          setLocationError('Could not access your location. Please enable location services or add an address manually.');
          setIsLocating(false);
        }
      );
    }
  }, [setUserLocation, savedAddresses.length]);
  
  // Handle saving a new or updated address
  const handleSaveAddress = (address: Omit<Address, 'id'>) => {
    const newAddress = {
      ...address,
      id: editingAddress?.id || `addr-${Date.now()}`,
    };
    
    let updatedAddresses;
    
    if (editingAddress) {
      updatedAddresses = savedAddresses.map(addr => 
        addr.id === editingAddress.id ? newAddress : addr
      );
    } else {
      updatedAddresses = [...savedAddresses, newAddress];
    }
    
    // If this is the only address or marked as default, set it as default
    if (updatedAddresses.length === 1 || newAddress.isDefault) {
      updatedAddresses = updatedAddresses.map(addr => ({
        ...addr,
        isDefault: addr.id === newAddress.id
      }));
      setSelectedAddressId(newAddress.id);
      setUserLocation({
        address: `${newAddress.addressLine1}, ${newAddress.city}, ${newAddress.state} - ${newAddress.pincode}`,
        coordinates: newAddress.coordinates
      });
    }
    
    setSavedAddresses(updatedAddresses);
    localStorage.setItem('savedAddresses', JSON.stringify(updatedAddresses));
    setIsAddressDialogOpen(false);
    setEditingAddress(undefined);
  };
  
  // Handle editing an existing address
  const handleEditAddress = (address: Address) => {
    setEditingAddress(address);
    setIsAddressDialogOpen(true);
  };
  
  // Handle selecting an address
  const handleSelectAddress = (address: Address) => {
    setSelectedAddressId(address.id || null);
    setUserLocation({
      address: `${address.addressLine1}, ${address.city}, ${address.state} - ${address.pincode}`,
      coordinates: address.coordinates || { lat: 0, lng: 0 }
    });
  };
  
  // Handle adding a new address
  const handleAddNewAddress = () => {
    setEditingAddress(undefined);
    setIsAddressDialogOpen(true);
  };

  useEffect(() => {
    setIsClient(true);
  }, []);

  if (!isClient) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-tendercuts-red"></div>
      </div>
    );
  }

  // Calculate values after ensuring we're on the client
  const subtotal = getCartTotal();
  const discount = couponApplied ? Math.round(subtotal * 0.1) : 0;
  const total = subtotal - discount + (deliveryFee || 0);

  const updateQuantity = (id: string, newQuantity: number) => {
    contextUpdateQuantity(id, newQuantity);
  };

  const removeItem = (id: string) => {
    removeFromCart(id);
  };

  const applyCoupon = () => {
    if (couponCode.toLowerCase() === "fresh10") {
      setCouponApplied(true);
    } else {
      alert("Invalid coupon code");
    }
  };

  const removeCoupon = () => {
    setCouponCode("");
    setCouponApplied(false);
  };

  return (
    <div className="bg-gray-50 min-h-screen">
      <div className="container mx-auto px-4 py-8">
        <h1 className="text-2xl font-bold text-gray-800 mb-6">Your Shopping Cart</h1>

        {cart.length > 0 ? (
          <div className="space-y-6">
            {/* Delivery Address Section */}
            <div className="bg-white rounded-lg shadow-sm p-6">
              <div className="flex justify-between items-center mb-4">
                <h2 className="text-lg font-semibold">Delivery Address</h2>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={handleAddNewAddress}
                  className="text-tendercuts-red border-tendercuts-red hover:bg-tendercuts-red/10"
                >
                  <PlusCircle className="h-4 w-4 mr-1" />
                  Add New Address
                </Button>
              </div>
              
              {isLocating && savedAddresses.length === 0 && (
                <div className="flex items-center text-gray-600 py-4">
                  <div className="animate-spin rounded-full h-5 w-5 border-t-2 border-b-2 border-tendercuts-red mr-2"></div>
                  Locating you...
                </div>
              )}
              
              {locationError && (
                <div className="bg-red-50 border-l-4 border-red-400 p-4 mb-4">
                  <div className="flex">
                    <div className="flex-shrink-0">
                      <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                        <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                      </svg>
                    </div>
                    <div className="ml-3">
                      <p className="text-sm text-red-700">{locationError}</p>
                    </div>
                  </div>
                </div>
              )}
              
              <div className="space-y-4">
                {savedAddresses.length > 0 ? (
                  savedAddresses.map((address) => (
                    <AddressCard
                      key={address.id}
                      address={address}
                      isSelected={selectedAddressId === address.id}
                      onSelect={() => handleSelectAddress(address)}
                      onEdit={() => handleEditAddress(address)}
                    />
                  ))
                ) : !isLocating && (
                  <div className="text-center py-8 border-2 border-dashed border-gray-300 rounded-lg">
                    <MapPin className="h-10 w-10 text-gray-400 mx-auto mb-2" />
                    <h3 className="text-lg font-medium text-gray-900">No saved addresses</h3>
                    <p className="mt-1 text-sm text-gray-500">Add an address to see it here</p>
                    <div className="mt-4">
                      <Button
                        onClick={handleAddNewAddress}
                        className="bg-tendercuts-red hover:bg-tendercuts-red/90"
                      >
                        <PlusCircle className="h-4 w-4 mr-2" />
                        Add Address
                      </Button>
                    </div>
                  </div>
                )}
                
                {savedAddresses.length > 0 && (
                  <div className="pt-2">
                    <Button
                      variant="ghost"
                      size="sm"
                      onClick={handleAddNewAddress}
                      className="text-tendercuts-red hover:bg-tendercuts-red/10"
                    >
                      <PlusCircle className="h-4 w-4 mr-1" />
                      Add another address
                    </Button>
                  </div>
                )}
              </div>
              
              {/* Address Form Dialog */}
              <AddressFormDialog
                open={isAddressDialogOpen}
                onOpenChange={(open) => {
                  setIsAddressDialogOpen(open);
                  if (!open) setEditingAddress(undefined);
                }}
                onSave={handleSaveAddress}
                initialData={editingAddress}
              />
            </div>
            
            <div className="flex flex-col lg:flex-row gap-6">
              {/* Cart Items */}
              <div className="flex-1">
                <div className="bg-white rounded-lg shadow-sm p-4 mb-6">
                  <div className="space-y-4">
                    {cart.map((item: CartItem) => (
                      <div
                        key={item.id}
                        className="flex flex-col sm:flex-row items-start sm:items-center gap-4 py-4 border-b border-gray-200 last:border-0"
                      >
                        <div className="w-full sm:w-20 relative aspect-square rounded-md overflow-hidden flex-shrink-0">
                          <Image
                            src={item.src || '/images/placeholder-product.jpg'}
                            alt={item.name}
                            fill
                            className="object-cover"
                            sizes="(max-width: 768px) 100vw, 20vw"
                          />
                        </div>

                        <div className="flex-1 w-full">
                          <Link
                            href={`/fish/${item.id}`}
                            className="font-medium text-gray-800 hover:text-tendercuts-red transition-colors line-clamp-2"
                          >
                            {item.name}
                          </Link>
                          
                          <div className="flex items-center justify-between mt-2">
                            <div className="flex items-center">
                              <button
                                onClick={() => updateQuantity(item.id, item.quantity - 1)}
                                className="h-8 w-8 flex items-center justify-center text-gray-500 border border-gray-300 rounded-l-md hover:bg-gray-100"
                              >
                                <Minus size={16} />
                              </button>
                              <span className="h-8 px-3 flex items-center justify-center text-gray-800 border-t border-b border-gray-300">
                                {item.quantity}
                              </span>
                              <button
                                onClick={() => updateQuantity(item.id, item.quantity + 1)}
                                className="h-8 w-8 flex items-center justify-center text-gray-500 border border-gray-300 rounded-r-md hover:bg-gray-100"
                              >
                                <Plus size={16} />
                              </button>
                            </div>
                            <div className="text-right">
                              <p className="font-semibold text-gray-800">
                                {formatPrice(item.price * item.quantity / 100)}
                              </p>
                              <button
                                onClick={() => removeItem(item.id)}
                                className="mt-2 text-tendercuts-red hover:text-tendercuts-red/80 text-sm flex items-center"
                              >
                                <Trash2 size={14} className="mr-1" />
                                Remove
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              </div>

              {/* Order Summary */}
              <div className="lg:w-96">
                <div className="bg-white rounded-lg shadow-sm p-6 sticky top-4">
                  <h2 className="text-lg font-semibold mb-4">Order Summary</h2>
                  
                  <div className="space-y-3">
                    <div className="flex justify-between">
                      <span className="text-gray-600">Subtotal</span>
                      <span>{formatPrice(subtotal / 100)}</span>
                    </div>

                    {couponApplied && (
                      <div className="flex justify-between text-green-600">
                        <span>Discount (10%)</span>
                        <span>-{formatPrice(discount / 100)}</span>
                      </div>
                    )}

                    <div className="flex justify-between">
                      <span className="text-gray-600">Delivery Fee</span>
                      <span>
                        {deliveryFee === 0 ? (
                          <span className="text-green-600 font-medium">Free</span>
                        ) : deliveryFee === -1 ? (
                          <span className="text-red-600">Not available in your area</span>
                        ) : (
                          formatPrice(deliveryFee / 100)
                        )}
                      </span>
                    </div>

                    <div className="border-t border-gray-200 pt-3 mt-3"></div>

                    <div className="flex justify-between font-bold text-lg">
                      <span>Total</span>
                      <span>
                        {deliveryFee === -1 ? '--' : formatPrice(total / 100)}
                      </span>
                    </div>
                  </div>

                  {/* Coupon Code */}
                  <div className="mt-6">
                    <h3 className="font-medium text-gray-800 mb-3">Apply Coupon</h3>
                    <div className="flex gap-2">
                      <Input
                        placeholder="Enter coupon code"
                        value={couponCode}
                        onChange={(e) => setCouponCode(e.target.value)}
                        disabled={couponApplied}
                        className="flex-1 w-full"
                      />
                      {couponApplied ? (
                        <Button
                          variant="outline"
                          className="whitespace-nowrap border-tendercuts-red text-tendercuts-red hover:bg-tendercuts-red/10 w-24"
                          onClick={removeCoupon}
                        >
                          Remove
                        </Button>
                      ) : (
                        <Button 
                          className="whitespace-nowrap bg-tendercuts-red hover:bg-tendercuts-red/90 w-24"
                          onClick={applyCoupon}
                        >
                          Apply
                        </Button>
                      )}
                    </div>
                  </div>

                  <Button
                    className={`w-full mt-6 ${deliveryFee === -1 ? 'bg-gray-400 cursor-not-allowed' : 'bg-tendercuts-red hover:bg-tendercuts-red/90'}`}
                    disabled={deliveryFee === -1 || isLocating}
                    asChild
                  >
                    <Link href="/checkout" className="flex items-center justify-center">
                      Proceed to Checkout
                      <ChevronRight size={16} className="ml-1" />
                    </Link>
                  </Button>

                  <p className="text-xs text-gray-500 mt-4 text-center">
                    Free delivery on orders above ₹500
                  </p>
                </div>
              </div>
            </div>
          </div>
        ) : (
          <div className="text-center py-12">
            <div className="w-24 h-24 mx-auto mb-4 text-gray-300">
              <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z" />
              </svg>
            </div>
            <h2 className="text-xl font-medium text-gray-700 mb-2">Your cart is empty</h2>
            <p className="text-gray-500 mb-6">Looks like you haven't added anything to your cart yet</p>
            <Link href="/category/fresh-fish">
              <Button className="bg-tendercuts-red hover:bg-tendercuts-red/90">
                Continue Shopping
              </Button>
            </Link>
          </div>
        )}
      </div>
      
      {cart.length > 0 && (
        <div className="bg-white py-8 border-t border-gray-100">
          <div className="container mx-auto px-4">
            <TopPicks title="You May Also Like" />
          </div>
        </div>
      )}
    </div>
  );
}
